<style lang="less" scoped>
  .centerBg{
    width: 100%;
    height: auto;
    padding: 20px 0 100px;
    background: #F4F5FA;
  }
  .centerLeft{
    float: left;
    width: 230px;
  }
  .centerRight{
    float: right;
    width: 950px;
    min-height: 610px;
    background: #fff;
    .centerTitle{
      padding: 0 33px;
      height: 60px;
      border-bottom: 1px solid #E3E4E5;
      ul{
        li{
          height: 58px;
          line-height: 58px;
          float: left;
          color: #333;
          font-size: 16px;
