            line-height: 65px;
            .data{
              color: #333;
              span{
                margin: 0 15px;
                color: #D7D8D9;
              }
            }
            .status{
              i{
                color: #999;
                font-style: normal;
              }
              span{
                color: #5389F7;
                margin-left: 25px;
                cursor: pointer;
              }
            }
          }
          dd{
            padding: 20px 22px;
            color: #808080;
            background: #F4F5FA;
          }
        }

      }
    }
  }
  .pages{
    margin: 30px 0;
    text-align: center;
  }
</style>



